body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #e5e5e5;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.fw-700 {
  font-weight: 700;
}

.size1-8 {
  font-size: 1.8rem;
}

.titulos {
  font-weight: 900;
  text-align: center;
  padding-top: 2rem;
}

.subtitulos {
  margin-top: 0;
  text-align: center;
  font-weight: 300;
  color: #3C4858;
  font-size: 1.8rem;
}

.sombra {
  box-shadow: 0px 20px 19px 3px rgba(0, 0, 0, 0.37);
}

.boton {
  color: white;
  font-weight: 700;
  text-decoration: none;
  font-size: 1.8rem;
  padding: 1rem 3rem;
  margin-top: 3rem;
  display: inline-block;
  text-align: center;
  border: none;
  display: block;
  flex: 0 0 100%;
  cursor: pointer;
}

.botonVerde {
  background-color: #71B100;
}

.centrarTexto {
  text-align: center;
}

.textoVerde {
  color: green;
}

.textoRojo {
  color: #af0000;
}

.quitarMargen {
  margin: 0;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

header {
  background-color: rgb(41, 64, 107);
  padding: 1rem 0 3rem 0;
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  header.inicio .contenido-header {
    min-height: 60rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 1.5;
  max-width: 60rem;
}

.footer {
  padding: 2rem 0;
  background-color: rgb(41, 64, 107);
}
.footer .contenido-footer {
  padding: 2.5rem 0 2.5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
  margin: 0;
  font-size: 1.5rem;
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: green;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
  }
}

label {
  display: block;
}

.Contacto p {
  font-size: 1.4rem;
  color: #4f4f4f;
  margin: 2rem 0 0 0;
}

legend {
  font-size: 2rem;
  color: #4f4f4f;
}

label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
label span {
  color: #af0000;
  font-size: 2rem;
}
label:first-of-type span {
  font-size: 1.2rem;
}

input:not([type=submit]),
textarea,
select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #e1e1e1;
  margin-bottom: 2rem;
  border-radius: 1rem;
}

input[type=radio] {
  width: auto;
  margin: 0;
}

select {
  -webkit-appearance: none;
  appearance: none;
}

textarea {
  height: 20rem;
  resize: none;
}

.formaContacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Contacto-bg {
  background-image: url(../../build/img/destacada3.jpg);
  height: 40rem;
  background-size: cover;
  background-repeat: no-repeat;
}

.formulario {
  border-radius: 3rem 3rem 0 0;
  background-color: white;
  padding: 5rem 5rem;
}
.formulario .caja1 {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.formulario .caja1 .caja2 {
  padding: 2rem 7rem;
}
.formulario.sesion {
  margin-top: 0;
  border-radius: 0;
}
.formulario img {
  width: 30%;
}
.formulario .Contacto_centrar {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
}

.Contacto {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.lidera {
  text-align: right;
  background-color: rgb(41, 64, 107);
  padding: 0.5rem 5rem 0.5rem 0;
  color: white;
}

.finanzas {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  background-color: rgba(83, 143, 233, 0.315);
}

.datos h2 {
  font-weight: bold;
}
.datos p {
  font-size: 1.5rem;
}

.referenciado .seleccione_texto {
  color: rgb(100, 100, 100);
}
.referenciado h1 {
  font-weight: bold;
}
.referenciado .alumno {
  font-weight: bold;
}
.referenciado .alumno p {
  color: rgb(17, 17, 85);
}
.referenciado .alumno p span {
  color: black;
  font-weight: 400;
}

.ePagado {
  background-color: rgba(0, 128, 0, 0.418) !important;
}

.eRechazado {
  background-color: rgba(255, 4, 4, 0.36) !important;
}

.eNoPagado {
  background-color: rgba(255, 255, 0, 0.377) !important;
}

.soporte {
  background-color: rgba(83, 143, 233, 0.315);
  padding: 1rem 1rem;
  width: 100%;
}

.bienvenidos h1 {
  font-size: 7rem;
}
.bienvenidos h2 {
  font-weight: bold;
}

.tipoUsuario {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 3rem;
  text-align: center;
  height: 20rem;
  margin: 6rem 0 10rem 0;
}
.tipoUsuario .tipo {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid rgb(189, 189, 189);
}
.tipoUsuario .tipo h2 {
  font-weight: bold;
  font-size: 2rem;
  padding: 0 2rem;
  margin: 1rem 0;
}
.tipoUsuario .tipo p {
  font-size: 1.5rem;
  padding: 0 1.5rem;
}
.tipoUsuario .tipo a {
  width: 100%;
  padding: 1rem 0;
  color: white;
  background-color: green;
  border: none;
  text-decoration: none;
  cursor: pointer;
}
.tipoUsuario .tipo div:last-of-type p {
  color: rgb(82, 82, 230);
  margin: 1rem 0 3rem 0;
}

.servicios div {
  display: grid;
  grid-template-columns: 4fr 1fr 2fr 3fr 1fr 1fr;
  gap: 1rem;
  justify-content: center;
}
.servicios div input {
  margin-bottom: 2rem;
}